// flex
@mixin flexbox($flex-direction: row, $justify-content: flex-start, $align-items: center) {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: $flex-direction;
  align-items: $align-items;
  justify-content: $justify-content;
}

// 文本超出显示省略号
@mixin overflowEllipsis($line: 1) {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $line;
}

// 全局调色盘
$blue: #457fff;
$green: #64c555;
$yellow: #f8e71c;
$orange: #f4be4f;
$red: #ea6b5e;
$purple: #bd10e0;
$light-blue: #1990ff;
$grey: #a9a9a9;
$black: #070707;
$white: #fff;

$sidebar-bgc: #4d474b;
$sidebar-color: #a5a4a5;
$chatroom-list-bgc: #fbfbfb;
$chatroom-list-bgc-em: #f1f1f1;
$chatting-panel-bgc: #f3f3f3;
$border: 1px solid #e6e6e6;
$message-item-bg: #f1f1f1;
$message-item-bg-me: #aae97a;
$message-item-shadow: 3px 3px 10px rgba(0, 0, 0, 0.117647);

// 字体设置
$font-color: #252525;
$font-color2: #585858;
$font-color3: #8a8a8a;
$font-color4: #bcbcbc;
$font-color4: #e3e3e3;

$fontweight-regular: 400;
$fontweight-medium: 500;
$fontweight-semibold: 600;

// 距离设置
$dist-small: 8px;
$dist-middle: 16px;
$dist-large: 24px;
$dist-huge: 32px;

// 效果设置
$default-transition: all .5s;
$main-border-radius: 4px;

.container {
  flex-grow: 1;
  padding: 20px;
  overflow-x: hidden;
  overflow-y: auto;

  :global .message-item {
    $messge-padding: 120px;

    @include flexbox(column, inherit, flex-start);
    color: $font-color;
    margin-top: 15px;
    padding-right: $messge-padding;

    &:first-child {
      margin-top: 0;
    }

    &-username {
      color: $font-color3;
      margin-bottom: 5px;
      font-weight: 400;
    }

    &-message {
      background-color: $white;
      padding: 5px 10px;
      margin: 0;
      box-shadow: $message-item-shadow;
      word-break: break-all;
      border-radius: 5px;
      display: inline-block;
      white-space: pre-wrap;
    }

    &.is-me {
      align-items: flex-end;
      padding-right: 0;
      padding-left: $messge-padding;

      .message-item-message {
        background-color: $message-item-bg-me;
      }
    }
  }
}
